import axios from 'axios';
//const url = 'http://localhost:7005'

const url = 'https://back-end-app-1.herokuapp.com'

export class Apireg {

    apiCall(method, url, data) {
        return axios({
            method,
            url,
            data
        })
    }


    getUser() {
        return this.apiCall('get', `${url}/register`)
    }

    addUser(email, password,confirmPassword, eircode) {
        return this.apiCall('post', `${url}/register`, { email, password,confirmPassword,eircode  })
    }
    


}